'use server'

import fs from 'fs';
import path from 'path';

const NOTES_DIR = process.env.NOTES_PATH || '/notes';

// Zorg dat de notes directory bestaat
if (!fs.existsSync(NOTES_DIR)) {
    fs.mkdirSync(NOTES_DIR, { recursive: true });
}

export async function getNotes() {
    const files = fs.readdirSync(NOTES_DIR);
    return files
        .filter(file => file.endsWith('.md'))
        .map(file => ({
            name: file.replace('.md', ''),
            path: file,
        }));
}

export async function readNote(filename: string) {
    const filePath = path.join(NOTES_DIR, filename.endsWith('.md') ? filename : `${filename}.md`);
    if (!fs.existsSync(filePath)) return '';
    return fs.readFileSync(filePath, 'utf-8');
}

export async function saveNote(filename: string, content: string) {
    const filePath = path.join(NOTES_DIR, filename.endsWith('.md') ? filename : `${filename}.md`);
    fs.writeFileSync(filePath, content, 'utf-8');
    return { success: true };
}

export async function createNote(name: string) {
    const filename = `${name}.md`;
    const filePath = path.join(NOTES_DIR, filename);
    if (fs.existsSync(filePath)) return { error: 'Bestand bestaat al' };
    fs.writeFileSync(filePath, '# ' + name, 'utf-8');
    return { success: true, name };
}

export async function deleteNote(filename: string) {
    const filePath = path.join(NOTES_DIR, filename.endsWith('.md') ? filename : `${filename}.md`);
    if (fs.existsSync(filePath)) {
        fs.unlinkSync(filePath);
    }
    return { success: true };
}
