'use client';

import { useState, useEffect } from 'react';
import ReactMarkdown from 'react-markdown';
import { FileText, Plus, Save, Sidebar as SidebarIcon } from 'lucide-react';
import { getNotes, readNote, saveNote, createNote } from '@/lib/notes';
import { motion, AnimatePresence } from 'framer-motion';

export default function Home() {
    const [notes, setNotes] = useState<{ name: string, path: string }[]>([]);
    const [activeNote, setActiveNote] = useState<string | null>(null);
    const [content, setContent] = useState('');
    const [sidebarOpen, setSidebarOpen] = useState(true);

    useEffect(() => {
        refreshNotes();
    }, []);

    const refreshNotes = async () => {
        const list = await getNotes();
        setNotes(list);
    };

    const handleSelectNote = async (filename: string) => {
        const text = await readNote(filename);
        setActiveNote(filename);
        setContent(text);
    };

    const handleSave = async () => {
        if (activeNote) {
            await saveNote(activeNote, content);
        }
    };

    const handleNewNote = async () => {
        const name = `Nieuwe Notitie ${notes.length + 1}`;
        const res = await createNote(name);
        if ('success' in res) {
            await refreshNotes();
            handleSelectNote(`${name}.md`);
        }
    };

    return (
        <div className="app-container">
            {/* Sidebar */}
            <motion.aside
                initial={false}
                animate={{ width: sidebarOpen ? 260 : 0 }}
                className="sidebar"
            >
                <div className="sidebar-header">
                    <span>Klompsidian</span>
                    <button onClick={handleNewNote} className="new-note-btn">
                        <Plus size={18} />
                    </button>
                </div>
                <div style={{ flex: 1, overflowY: 'auto' }}>
                    {notes.map((note) => (
                        <div
                            key={note.path}
                            className={`note-item ${activeNote === note.path ? 'active' : ''}`}
                            onClick={() => handleSelectNote(note.path)}
                        >
                            <FileText size={16} />
                            <span>{note.name}</span>
                        </div>
                    ))}
                </div>
            </motion.aside>

            {/* Main Content */}
            <main className="main-content">
                <div style={{ padding: '10px 20px', borderBottom: '1px solid var(--border)', display: 'flex', justifyContent: 'space-between' }}>
                    <button onClick={() => setSidebarOpen(!sidebarOpen)} style={{ background: 'transparent', border: 'none', color: 'var(--foreground)', cursor: 'pointer' }}>
                        <SidebarIcon size={20} />
                    </button>

                    <div style={{ display: 'flex', gap: '10px', alignItems: 'center' }}>
                        {activeNote && (
                            <>
                                <span style={{ fontSize: '0.9rem', color: '#888' }}>{activeNote}</span>
                                <button onClick={handleSave} className="new-note-btn" style={{ padding: '4px 12px' }}>
                                    <Save size={16} />
                                </button>
                            </>
                        )}
                    </div>
                </div>

                {activeNote ? (
                    <div className="editor-container">
                        <div className="glass-panel">
                            <textarea
                                className="markdown-input"
                                value={content}
                                onChange={(e) => setContent(e.target.value)}
                                placeholder="Typ hier je markdown..."
                            />
                        </div>
                        <div className="glass-panel preview-content">
                            <ReactMarkdown>{content}</ReactMarkdown>
                        </div>
                    </div>
                ) : (
                    <div style={{ display: 'flex', alignItems: 'center', justifyContent: 'center', height: '100%', color: '#666' }}>
                        Selecteer een notitie of maak een nieuwe aan
                    </div>
                )}
            </main>
        </div>
    );
}
